<template>
    <h1>jobs</h1>
    <div v-if="jobs.length">
        <div v-for="job in jobs" :key="job.id">
        <router-link :to="{ name: 'jobDetails', params: { id: job.id } }">
            <h2>{{ job.title }}</h2>
        </router-link>
    </div>
</div>
<div v-else>
    <p>loading</p>
</div>
    
</template>

<script>
export default {
    data() {
        return {
            jobs: [],
        };
    },
    mounted() {
        fetch("http://localhost:3001/jobs")
            .then((res) => res.json())
            .then((data) => {
                this.jobs = data;
                console.log(data)
            })
            .catch((err) => console.log(err.message));
    },
};
</script>

<style></style>
